import React from "react";
import {uniqueId} from "lodash";
import {format,scaleLinear,ScaleSequential} from "d3"
interface Props {
    colorScale: ScaleSequential<any,any>,
    range: [number,number]|Array<number>|[undefined,undefined],
    style?: any,
    height?: number,
    barHeight?: number
}


const ColorLegend = ({colorScale,range,height=30,barHeight=10,style={},...other}:Props)=>{
    const id = React.useMemo(()=>uniqueId(),[]);
    const {ticks,cloneColor} = React.useMemo(()=>{
        const _range = colorScale.domain();
        const newRange = (_range[0]>_range[1])?range.slice().reverse():range.slice();
        const scale = colorScale.copy().domain([newRange[0]??0,newRange[1]??0]);
        const scalep = scaleLinear().domain([newRange[0]??0,newRange[1]??0].sort((a,b)=>a-b)).range([0,100])
        return {ticks:scalep.ticks(5).sort((a,b)=>a-b).map(v=>[v,scalep(v)]),cloneColor:scale};
    },[colorScale,range]);
    return <svg style={{width:'100%',height,...style}}>
        <defs>
            <linearGradient id={"linear-gradient"+id}>
                {ticks.map((t, i, n) => <stop key={t[0]} offset={`${t[1]}%`} stopColor={cloneColor(t[0])}/>)}
            </linearGradient>
        </defs>
        <rect width={'100%'} height={barHeight} style={{fill:`url(#${"linear-gradient"+id})`}}/>
        {ticks.map((t, i, n) => <text key={t[0]} x={`${t[1]}%`} dy={"1rem"} y={barHeight} fill={"currentColor"}>{Math.abs(t[0])>999?format('.2s')(t[0]):t[0]}</text>)}
    </svg>
}
export default ColorLegend
